* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 36px;
}

body {
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.square {
  width: 400px;
  height: 400px;
  border: 2px solid #000;
  box-shadow: 2px 2px 5px #000;
  border-radius: 0.25rem;
  display: grid;
  place-content: center;
}

.square p {
  text-align: center;
}

form {
  width: 400px;
}

label {
  position: absolute;
  left: -99999px;
}

input[type="text"] {
  margin-top: 0.5rem;
  padding: 0.25rem;
  width: 100%;
  font-size: 1rem;
  box-shadow: 2px 2px 5px #141414;
  border-radius: 0.25rem;
  outline: none;
}

button {
  width: 100%;
  min-height: 48px;
  margin-top: 0.5rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  box-shadow: 2px 2px 1px #141414;
  padding: 0.25rem;
}

button:hover {
  background: rgb(56, 96, 230);
  color: rgb(238, 234, 234);
}

@media only screen and (min-width: 375px) and (max-width: 400px) {
  .form {
    width:fit-content;
    margin-left: 12px;
  }
  .square {
    width: 350px;
    height: 350px;
  }
  
  input[type="text"] {
    width: 350px;
    font-size: 0.7rem;
  }

  button {
    width: 350px;
    font-size: 0.7rem;
  }
}